.Box {
  background: #0a0a0a;
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}


.Logo {
  padding: 16px;
  font-weight: bold;
  color: #f5f5f5;
}


.Main_Content {
  background: rgba(20, 20, 20, 0.85);
  border: 1.5px solid rgba(255, 255, 255, 0.1);
  color: #e0e0e0;
  width: 60%;
  max-width: 800px;
  line-height: 1.6;
  font-size: calc(0.9rem + 0.4vw);
  backdrop-filter: blur(8px);
  border-radius: 12px;
  padding: 2rem;
  margin-top: 6rem;
  margin-left: 4rem;
  text-align: left;
  font-family: "Poppins", "Segoe UI", Arial, sans-serif;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.7);
  transition: transform 0.3s ease, border-color 0.3s ease;
}

 .Main_Content:hover {
  border-color: black; 
  cursor: pointer;
  transform: translateY(-3px);
} 

.Main_Content .Main {
  width: 100%;
}

.Social_Links ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 1.2rem;
  padding: 0;
}

.Social_Links ul li {
  list-style: none;
}

.Social_Links ul li a {
  font-size: 16px;
  text-decoration: none;
  color: #39ff14;
  padding: 6px 14px;
  border: 1.5px solid #39ff14;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.Social_Links ul li a:hover {
  background: #39ff14;
  color: #0a0a0a;
  transform: translateY(-2px) scale(1.05);
  box-shadow: 0 4px 12px rgba(57, 255, 20, 0.4);
}

@media (max-width: 1024px) {
  .Main_Content {
    width: 75%;
    margin-left: 2rem;
    font-size: calc(0.85rem + 0.4vw);
  }
}

@media (max-width: 768px) {
  .Main_Content {
    width: 85%;
    margin: 5rem auto 0 auto;
    font-size: calc(0.8rem + 0.5vw);
  }

  .Social_Links ul {
  }
}

@media (max-width: 480px) {
  .Main_Content {
    width: 92%;
    margin: 4rem auto 0 auto;
    padding: 1.2rem;
    font-size: 1rem;
  }

  .Social_Links ul {
    flex-direction: column;
    align-items: center;
  }

  .Social_Links ul li a {
    width: 100%;
    text-align: center;
  }
}
