.Maincontent {
  background-color: #12343b;
  height: fit-content;
  width: 98%;
  border-radius: 10px;
}
.Mainconnect_head {
  padding: 2rem;
}
.Mainconnect_head p {
  font-size: 1.5rem;
  color: #fbe4e0;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  width: 100%;
  margin-left: 10px;
}
.Mainconnect_head h1 {
  margin-left: 10px;
  color: snow;
  font-weight: 400;
  padding-block: 0.4rem;
  font-size: 2.4rem;
  font-family: "Times New Roman", Times, serif;
}
.Mainconnect_contact {
  margin-top: -2rem;
  display: flex;
  justify-content: space-between;
}

.Maincontent_left form {
  display: flex;
  flex-direction: column;
  margin: 2rem;
}
.Maincontent_left form input {
  padding: 1rem;
  background: none;
  border: none;
  border: 1px solid snow;
  color: #fbe4e0;
  width: 35vw;
  border-radius: 5px;
  margin: 0.7rem;
}
.Maincontent_left form textarea {
  padding: 1rem;
  padding-top: 3rem;
  background: none;
  border: none;
  border: 1px solid snow;
  color: #fbe4e0;
  width: 35vw;
  border-radius: 5px;
  margin: 0.7rem;
}
.Maincontent_left form button {
  padding: 1rem;
  border: 1px solid snow;
  margin: 0.7rem;
  cursor: pointer;
}
.Maincontent_right {
  margin-top: 1rem;
  width:30vw;
  border: 1px solid snow;
  height: max-content;
  border-radius: 10px;
  padding-bottom: 0.7rem;
  text-align: center;
  margin-right: 10rem;
}
.Maincontent_right p{
  color: tan;
  padding: 1.5rem;
  user-select: none;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.Maincontent_right ul li{
  text-decoration: none;
  list-style: none;
  padding-block: 0.7rem;

}
.Maincontent_right ul li a {
  text-decoration: none;
  color: snow;
  font-size: 1.2rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.Maincontent_right ul li a:hover {
  color: #fbe4e0;
}
.Maincontent_right ul li i{
  margin-right: 10px;
}

@media screen and (max-width: 600px) {
  .Mainconnect_contact {
    text-align: center;
  }
  .Mainconnect_head h1 {
    font-size: 2.1rem;
    padding-block: 0.4rem;
  }
  .Mainconnect_contact {
    display: block;
    flex-direction: column;
  }
  .Maincontent_left form{
    align-items: center;
  }
  .Maincontent_left form input {
    width: 110%;
    margin-inline: 0;
  }
  .Maincontent_left form textarea {
    width: 110%;
    margin: 0;
  }
  .Maincontent_right {
    text-align: center;
    width: 90%;
    margin: 1rem;
  }
} 

/* Responsive adjustments */
@media (max-width: 768px) {
  .MainConnect {
    font-size: 1rem;
    padding: 0.4rem 1.2rem;
  }
}

/* Animations */
@keyframes glow {
  0% {
    box-shadow: 0 0 5px rgba(139, 92, 246, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.8);
  }
  100% {
    box-shadow: 0 0 5px rgba(139, 92, 246, 0.5);
  }
}